@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&family=Montserrat:wght@400;500;600;700;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}
#root {
  width: 1920px;
}

ul {
  color: black;
  list-style: none;

}

.active {
  color: black;
  font-weight: bold;
  list-style: none;
}

span {
  display: none;
}

a:visited {
  color: black;
  text-decoration: none;
}
/* Home */

.backGround {
  position: relative;
}
.backGround .logo {
  position: absolute;
  left: 500px;
  cursor: pointer;
  transform: scale(1.0);
  transition: transform .2s;
}
.backGround .logo:hover {
  transform: scale(1.1);
  transition: transform .2s;
}

.icons img {
  width: 120px;
  height: 120px;
  transform: scale(1.0);
  transition: transform .2s;
}

.icons img:hover {
  transform: scale(1.1);
  transition: transform .2s;
}

.icons>li:first-child {
  position: absolute;
  left: 20px;
  top: 550px;
}

.icons>li:nth-child(2) {
  position: absolute;
  left: 20px;
  top: 700px;
}

.icons>li:nth-child(3) {
  position: absolute;
  left: 1760px;
  top: 550px;
}

.icons>li:last-child {
  position: absolute;
  left: 1760px;
  top: 700px;
}

.wrapper {
  background-image: url('../public/images/bg.png');
  background-repeat: no-repeat;
  width: 100%;
  height: 968px;
  overflow: hidden;
  margin: 0 auto;
}
.member .mark {
  position: absolute;
  text-align: right;
  background-color: #fff;
  width: 140px;
  padding: 10px;
  line-height: 1.5rem;
  left: 20px;
}
.member .mark li {
  font-size: 0;

}
.member .mark li a{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.member .mark li p {
  color: black;
  font-family: 'Montserrat';
  letter-spacing: 0.1em;
  font-size: 15px;
}

.member .simg {
  box-sizing: border-box;
  border: none;
  position: relative;
  width: 100px;
  left: 60px;
} 

.members_crop {
  position: relative;
}

.members_crop li:nth-child(1) {
  position: absolute;
  scale: 1;
  transition: transform .2s;
  left: 1355px;
  top: 228px;
}

.members_crop li:nth-child(1):hover {
  transform: scale(1.05);
  transition: transform .2s;
}

.members_crop li:nth-child(2) {
  position: absolute;
  scale: 1;
  transition: transform .2s;
  left: 838px;
  top: 230px;
}

.members_crop li:nth-child(2):hover {
  transform: scale(1.1);
  transition: transform .2s;
}

.members_crop li:nth-child(3) {
  position: absolute;
  scale: 1.12;
  transition: transform .2s;
  left: 213px;
  top: 291px;
}

.members_crop li:nth-child(3):hover {
  transform: scale(1.05);
  transition: transform .2s;
}

.members_crop li:nth-child(4) {
  position: absolute;
  scale: 1;
  transition: transform .2s;
  left: 455px;
  top: 230px;
}

.members_crop li:nth-child(4):hover {
  transform: scale(1.05);
  transition: transform .2s;
}

/* Home 종료 */
.member {
  text-align: center;
}



.about {
  position: relative;
  width: 1600px;
  height: 800px;
  margin: 100px auto 0 auto;
  background-color: rgba(255, 151, 206, 0.5);
}
.about button {
  position: absolute;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  top: -25px;
  right: -25px;
  font-size: 20px;
  z-index: 20;
  transition: transform .2s;
}
.about button:hover {
  background-color: #d7d7d7;
  transition: transform .2s;
} 

.about .profile_img {
  width: 900px;
  height: 800px;
  margin: 0 auto;
  display: inline-block;
  float: left;
  background-color: rgb(255, 255, 255);
  overflow: hidden;
}
.about .board {
  position: relative;
  width: 700px;
  height: 800px;
  margin: 0 auto;
  display: inline-block;
  text-align: left;
  background-color: rgb(255, 255, 255);
}
.board .category  {
  width: 700px;
  float: left;
  text-align: center;
  font-size: 1.8em;

}
.board .category  li {
  display: inline-block;
  width: 50%;
  padding: 5px;
  height: 50px;
}
.board .category li:hover {
  background-color: #ccc;
  cursor: pointer;
}

.profile_text {
  display: inline-block;
  position: absolute;
  padding: 20px;
  font-family: 'Montserrat';
  text-align: left;
  width: 200px;
  height: 100px;
  bottom:150px;
  left: 80px;
}
.profile_text h2 {
  margin: 20px 0;
}
.profile_text>div {
  width: 200px;
  font-size: 1.2em;
}

.pro_icon {
  position: absolute;
  top: 150px;
  right: 50px;
  width: 80px;
  height: 390px;
  border: 1px solid #ccc;
  border-radius: 50px;
  text-align: center;
  vertical-align: middle;
  line-height: 100px
}
.pro_icon li {
  margin-top: 30px;
  line-height: 30px
}
.pro_icon li a img{
  width: 50px;
  height: 50px;
}

.pro_icon img {
  transform: scale(1.0);
  transition: transform .2s;
}

.pro_icon img:hover {
  transform: scale(1.1);
  transition: transform .2s;
}





/* 갤러리 */
.gallery {
  width: 700px;
  height: 750px;
  padding: 20px 50px 50px 50px;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  overflow: hidden;
}
.gallery li {
  overflow: hidden;
}
.g_img{
  width: 130px;
  margin: 20px;
  height: auto;
  transform: scale(1);
  transition: transform .2s;
}

.g_img:hover {
  transform: scale(1.1);
  transition: transform .2s;
}

